import Request from './request'

const http = new Request()

http.setConfig((config) => { /*  */
	config.baseUrl = 'http://student-app-api.zsdx.cn/api' /*  */
	config.header = {
		...config.header,
		'zsdx-app-type': 1,
		'zsdx-app-uuid': '',
		'zsdx-device-info': JSON.stringify({"phone_model":"SM-G900P","phone_platform":"android","system_version":"Android 5.0","language":"zh-CN"}),
		'zsdx-session-ticket': '097e8ebd973e35fa2e3032877bd619849657',
		'zsdx-version': '1.2.701'
	}
	return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
http.validateStatus = (statusCode) => {
	return statusCode === 200
}

